body,
* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

main {
  display: flex;
  flex-direction: column;
  justify-items: center;
  background-color: #E3E3E3;
  width: 100%;
  min-height: calc(100vh - 100px);
}

footer {
  background-color: rgb(35, 35, 35);
  color: white;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.detail-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #3BB4C1;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

h1 {
  margin: 10px;
}

.likeButtonContainer {
  border: 0;
