import FavoriteRestaurantIdb from '../../public/data/favorite'
import templateCreator from './templateCreator'
import getRestaurant from './getRestaurant'

const likeButtonPresenter = {
  async init ({ id, likeButtonContainer }) {
    this._id = id
    this._likeButtonContainer = likeButtonContainer

    await this._renderLikeButton()
    this._likeButtonContainer.addEventListener('click', async () => {
      await this._handleFavorite()
    })
  },

  async _renderLikeButton () {
    console.log('rendering button')
    try {
      const restaurant = await FavoriteRestaurantIdb.getFavoriteRestaurant(this._id)
      if (restaurant) {
        console.log('rendered unlike button')
        this._likeButtonContainer.innerHTML = templateCreator.createLikedButtonTemplate()
      } else {
        console.log('rendered like button')
        this._likeButtonContainer.innerHTML = templateCreator.createLikeButtonTemplate()
      }
    } catch (error) {
      console.error('Error in render:', error)
    }
  },

  async _handleFavorite () {
    console.log('button clicked')
    const restaurantExist = await FavoriteRestaurantIdb.getFavoriteRestaurant(this._id)

    if (restaurantExist) {
      await FavoriteRestaurantIdb.deleteRestaurant(this._id)
      console.log('restaurant exists')
      this._renderLikeButton()
    } else {
      const restaurant = await getRestaurant(this._id)
      console.log('restaurant does not exist')
      await FavoriteRestaurantIdb.addRestaurant(restaurant)
      this._renderLikeButton()
    }
  }

}

export default likeButtonPresenter
